@mixin hover($_property, $_toValue, $speed) {
  transition: #{$_property} $speed;
  &:hover {
    #{$_property}: $_toValue;
    @content;
  }
}

@mixin active($_property, $_toValue) {
  transition: #{$_property} 300ms;
  &:active {
    #{$_property}: $_toValue;
    @content;
  }
}

@mixin breakpoint($width) {
  @media (min-width: $width) {
    @content;
  }
}

@mixin media-breakpoint($width, $column) {
  @media (min-width: $width) {
    grid-template-columns: repeat($column, 1fr);
    @content;
  }
}

@mixin grid($column) {
  display: grid;
  grid-template-columns: repeat($column, 1fr);
  align-items: center;
  column-gap: 20px;
  @content;
}

@mixin flex($direction, $gap) {
  display: flex;
  flex-direction: $direction;
  gap: $gap;

  @content;
}

@mixin font-style($weight, $size, $line-height) {
  @extend %font-family;

  font-weight: $weight;
  font-size: $size;
  line-height: $line-height;
}

@mixin breakpoint-font-style($size, $line-height) {
  font-size: $size;
  line-height: $line-height;
}

@mixin content-centering($width) {
  max-width: $width;
  margin-inline: auto;
}

@mixin animation-brightness($start, $finish) {
  animation-name: brightness;
  animation-duration: 3s;
  animation-direction: alternate;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;

  @keyframes brightness {
    0% {
      filter: brightness($start);
    }

    100% {
      filter: brightness($finish);
    }
  }
}
